button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.habit {
  font-size: 2.5em;
  padding: 0.5em;
  list-style: none;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 1.8rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.4rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: #000;
  color: #fff;
  border-radius: 50%;
}

.habit-button {
  font-size: 1em;
  margin: 0 0.1em;
  background-color: transparent;
}

.habit-button:hover {
  opacity:0.8;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  color: red;
}